//Proxima Nova light
@font-face {
  font-family: 'Proxima';
  src: url('../../fonts/ProximaNova-Light.woff') format('woff'),
       url('../../fonts/ProximaNova-Light.svg#ProximaNova-Light') format('svg');
  font-weight: 300;
  font-style: normal;
}


